import { POST, PUT } from '../../../api/index'
import { Header } from '../../../api/header'
import { HalfCircleSpinner } from "epic-spinners";
import { success, error } from "../../../service/MessageBox"

export default {
  name: "input-client",
  components: { HalfCircleSpinner },
  props: ["close", "loadData", "dataEdit", "isShow", "resetdetail", "disableView", "onView", "textTitle"],
  data() {
    return {
      isLoading: true,
      disable: false,
      id: null,
      name: null,
      npwpNo: null,
      address: null,
      businessLineID: null,
      phoneNo: null,
      website: null,
      npwpAddress: null,
      parentCompanyID: null,
      tanggalTagihan: null,
      clientCompanyCode: null,
      value: [],
      options: [
        { name: 'Vue.js', code: 'vu' },
        { name: 'Javascript', code: 'js' },
        { name: 'Open Source', code: 'os' }
      ]
    };
  },
  beforeMount: function () { },
  mounted() { },
  methods: {
    save: function () {
      try {
        this.isLoading = true
        let dataReq = {
          Id: this.dataEdit.Id,
          Name: this.dataEdit.Name,
          NPWPNo: this.dataEdit.NPWPNo,
          Address: this.dataEdit.Address,
          BusinessLineID: this.dataEdit.BusinessLineID,
          PhoneNo: this.dataEdit.PhoneNo,
          Website: this.dataEdit.Website,
          NPWPAddress: this.dataEdit.NPWPAddress,
          ParentCompanyID: this.dataEdit.ParentCompanyID,
          TanggalTagihan: this.dataEdit.TanggalTagihan,
          ClientCompanyCode: this.dataEdit.ClientCompanyCode
        }
        if (dataReq.Id === null) {
          POST("clientcompany", dataReq, Header()).then((res) => {
            this.loadData()
            this.isLoading = false
            success("Data Berhasil Disimpan")
            this.close();
          })
        } else {
          PUT("clientcompany", dataReq, Header()).then((res) => {
            this.loadData()
            this.isLoading = false
            success("Data Berhasil Di Update")
            this.close();
          })
        }
      } catch (err) {
        this.isLoading = false
        error("Error")
      }
    },
    onClose: function () {
      this.onView("", false);
      this.resetdetail();
    },
  },
};
