import inputClient from "./inputClient";
import { POST, GET, PUT, DELETE } from "../../api/index";
import { HalfCircleSpinner } from "epic-spinners";
import { success, deleteData, error } from "../../service/MessageBox";

export default {
  name: "client",
  components: { inputClient, HalfCircleSpinner },
  created() {
    this.loadData();
  },
  beforeMount: function() {},
  props: [],
  data() {
    return {
      pagination: {
        page: 1,
        rowsPerPage: 5
      },
      totalData: 0,
      isLoading: true,
      isShow: false,
      disableView: false,
      textTitle: "",
      dataClient: [],
      dataEdit: {
        Id: null,
        Name: null,
        NPWPNo: null,
        Address: null,
        BusinessLineID: null,
        PhoneNo: null,
        Website: null,
        NPWPAddress: null,
        ParentCompanyID: null,
        TanggalTagihan: null,
        ClientCompanyCode: null
      },
      headers: [
        {
          text: "Aksi",
          value: "aksi",
          sortable: false,
          align: "left",
          class: ""
        },
        {
          text: "Nama",
          value: "name",
          align: "center"
        },
        {
          text: "Phone No",
          value: "phoneNo",
          align: "center"
        },
        {
          text: "Parent Company ID",
          value: "parentCompanyID",
          align: "center"
        },
        {
          text: "Client Company Code",
          value: "clientCompanyCode",
          align: "center"
        }
      ]
    };
  },
  watch: {
    pagination: {
      handler() {
        try {
          this.isLoading = true;
          const { page, rowsPerPage } = this.pagination;
          POST(`clientcompany/clientcompany_cr/${page}/${rowsPerPage}`).then(res => {
            this.dataClient = res.Data.map(x => {
              return {
                id: x.Id,
                name: x.Name,
                npwpNo: x.NPWPNo,
                address: x.Address,
                businessLineID: x.BusinessLineID,
                phoneNo: x.PhoneNo,
                website: x.Website,
                npwpAddress: x.NPWPAddress,
                parentCompanyID: x.ParentCompanyID,
                tanggalTagihan: x.TanggalTagihan,
                clientCompanyCode: x.ClientCompanyCode
              };
            });
            this.isLoading = false;
            this.totalData = res.TotalData;
          });
        } catch (err) {
          this.isLoading = false;
          error("Error");
        }
      },
      deep: true
    }
  },
  computed: {},
  methods: {
    loadData: function() {
      try {
        this.isLoading = true;
        const { page, rowsPerPage } = this.pagination;
        POST(`clientcompany/clientcompany_cr/${page}/${rowsPerPage}`).then(res => {
          this.dataClient = res.Data.map(x => {
            return {
              id: x.Id,
              name: x.Name,
              npwpNo: x.NPWPNo,
              address: x.Address,
              businessLineID: x.BusinessLineID,
              phoneNo: x.PhoneNo,
              website: x.Website,
              npwpAddress: x.NPWPAddress,
              parentCompanyID: x.ParentCompanyID,
              tanggalTagihan: x.TanggalTagihan,
              clientCompanyCode: x.ClientCompanyCode
            };
          });
          this.isLoading = false;
          this.totalData = res.TotalData;
        });
      } catch (err) {
        this.isLoading = false;
        error("Error");
      }
    },
    toggleInputDialog: function(exFunc) {
      this.isShow = !this.isShow;
      this.textTitle = "Tambah Client";
    },
    detailData: function(id) {
      try {
        this.isLoading = true;
        POST("clientcompany/clientcompany_cr", {Id: id}).then(res => {
          this.dataEdit = res[0];
          this.textTitle = "Ubah Client";
          this.isShow = !this.isShow;
          this.isLoading = false;
        });
      } catch (err) {
        this.isLoading = false;
      }
    },
    delData: function(id) {
      try {
        this.isLoading = true;
        DELETE("clientcompany/" + id).then(res => {
          success("Data Berhasil Dihapus");
          this.loadData();
          this.isLoading = false;
        });
      } catch (err) {
        this.isLoading = false;
        error("Error");
      }
    },
    onDelete: function(id) {
      deleteData(id, this.delData);
    },
    onView: function(id, isOpen) {
      var vm = this
      if (isOpen) {
        try {
          if (id !== "") {
            this.isLoading = true;
            POST("clientcompany/clientcompany_cr", {Id: id}).then(res => {
              vm.dataEdit = res[0];
              vm.isShow = isOpen;
              vm.disableView = isOpen;
              vm.textTitle = "Lihat Client";
              vm.isLoading = false;
            });
          } else {
            this.disableView = isOpen;
          }
        } catch (err) {
          isOpen = false;
          this.isLoading = false;
        }
      }      
      this.isShow = isOpen;
    },
    resetdetail: function() {
      this.dataEdit = {
        Id: null,
        Name: null,
        NPWPNo: null,
        Address: null,
        BusinessLineID: null,
        PhoneNo: null,
        Website: null,
        NPWPAddress: null,
        ParentCompanyID: null,
        TanggalTagihan: null,
        ClientCompanyCode: null
      };
      this.disableView = false
    }
  }
};
